<script>
  import Radio from "./radio.svelte";
  import Input from "./input.svelte";

  let disabled = false;
  let message = "";
  let messageClass = "";
  let formClass = "";
  let otherClass = "";

  const exposure = [
    { label: "Facebook", value: "facebook" },
    { label: "Mail Flyer", value: "flyer" },
    { label: "In-Store", value: "store" },
    { label: "Friends", value: "friend" },
    { label: "Other", value: "other" }
  ];

  const gender = [
    { label: "Male", value: "male" },
    { label: "Female", value: "female" }
  ];

  const yn = [{ label: "Yes", value: "Yes" }, { label: "No", value: "No" }];

  const submit = e => {
    e.preventDefault();

    disabled = true;

    let checked = false;
    const inputs = e.target.querySelectorAll(`input[type="radio"][name="366"]`);
    for (let input of inputs) {
      if (input.checked) {
        checked = true;
        break;
      }
    }

    if (!checked) {
      message = "Exposure is required a field";
      messageClass = "error";
      disabled = false;
      return false;
    }

    let form = new FormData();
    document.querySelectorAll("form input[name]").forEach(input => {
      if (input.name && (input.type != "radio" || input.checked)) {
        form.append(input.name, input.value);
      }
    });

    fetch("post.php", {
      method: "POST",
      body: form
    })
      .then(r => r.json())
      .then(r => (r == true ? success() : error(r)))
      .catch(e => error(e));

    return false;
  };

  const success = () => {
    message = "Thank you for your submission";
    formClass = "success";

    setTimeout(() => {
      location.reload();
    }, 1000);
  };

  const error = e => {
    disabled = false;
    message = "There was an error submitting your response";
    messageClass = "error";
    console.error(e);
  };
</script>

<style>
  main {
    min-height: 100vh;
    box-sizing: border-box;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    padding: 40px;
  }
  #message {
    color: #fff;
  }
  #message.error {
    color: #ff6961;
  }
  form {
    opacity: 1;
    transition: 500ms;
  }
  form.success {
    opacity: 0;
  }
  .grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    margin-bottom: 30px;
  }

  .grid .label {
    grid-column: span 2;
    margin: 0;
  }

  .span {
    grid-column: span 2;
  }
</style>

<main>
  <h1>Thanksgiving Survey</h1>
  <p id="message" class={messageClass}>{message}</p>
  <form action="post.php" method="POST" on:submit={submit} class={formClass}>
    <Input
      label="Customer Total"
      type="number"
      name="367"
      placeholder="0.00"
      required="true"
      {disabled} />
    <div class="grid">
      <p class="label">Exposure*</p>
      <Radio name="366" options={exposure} {disabled} />
      <div class="span {otherClass}">
        <Input label="Other" name="371" type="text" {disabled} />
      </div>
    </div>
    <div class="grid">
      <p class="label">Gender</p>
      <Radio name="368" options={gender} {disabled} />
    </div>
    <div class="grid">
      <p class="label">First time at store</p>
      <Radio name="369" options={yn} {disabled} />
    </div>
    <Input type="submit" value="Submit" {disabled} />
  </form>
</main>
